@import url(https://fonts.googleapis.com/css?family=Russo+One);
body {
 margin: 0;
 font-family: 'Russo One', sans-serif;
 background-color: rgb(148, 116, 64);
}

.app {
 border: solid rgb(255, 165, 0) 0.3vw;
 width: 60vw;
 height: 30vw;
 margin: auto;
 margin-top: calc(50vh - 15vw);
 background-color: rgb(179, 179, 179);
 display: flex;
 align-items: center;
 justify-content: space-evenly;
 box-shadow: 0.1vw 1vw 2vw #000000;
}

.keys-container {
 border: brown;
 width: 30vw;
 height: 24vw;
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 grid-template-rows: repeat(3, 1fr);
 grid-column-gap: 1vw;
 grid-row-gap: 1vw;
}

.drum-pad {
 font-size: 2.5vw;
 display: flex;
 align-items: center;
 justify-content: center;
}

.settings {
 width: 20vw;
 height: 24vw;

 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: space-evenly;
}

.switch {
 width: 5vw;
 height: 2vw;
 background-color: black;
 display: flex;
}

.knob {
 transition: 0.5s;
 height: 1.5vw;
 margin-top: 0.2vw;
 margin-left: 0.2vw;
 margin-right: 0.2vw;
 width: 2.25vw;
 background-color: blue;
}

.header {
 margin: 0;
 font-size: 1vw;
}
.display-bar {
 display: flex;
 align-items: center;
 justify-content: center;
 width: 10vw;
 background-color: grey;
 height: 3vw;
 font-size: 1.3vw;
}
.volume-bar {
 width: 14vw;
 background-color: rgb(71, 71, 71);
 -webkit-appearance: none;
 box-shadow: 0.2vw 0.2vw 0.2vw #000000;
}
.volume-bar::-ms-track {
 cursor: pointer;
}

.volume-bar::-webkit-slider-thumb {
 -webkit-appearance: none;
 height: 2.5vw;
 width: 0.75vw;
 background: blue;
 cursor: pointer;
 margin-top: -1vw;
 margin-bottom: -1vw; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
 box-shadow: 0.2vw 0.2vw 0.2vw #000000; /* Add cool effects to your sliders! */
}

