.keys-container {
 border: brown;
 width: 30vw;
 height: 24vw;
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 grid-template-rows: repeat(3, 1fr);
 grid-column-gap: 1vw;
 grid-row-gap: 1vw;
}

.drum-pad {
 font-size: 2.5vw;
 display: flex;
 align-items: center;
 justify-content: center;
}
